
import server1Icon from "@/assets/svg/ic_server.svg";
import server2Icon from "@/assets/svg/ic_server2.svg";
import userOutlineSvg from "@/assets/svg/ic_user_outline.svg";
import axios from "axios";
import sensorServices from "../sensor/sensor.services";
import gatewayServices from "../gateway/gateway.services";
export default {
    name: 'dashboard-main',
    components: {

        server1Icon,
        server2Icon,
        userOutlineSvg
    },
    data() {
        return {
            loadingAvg: false,
            avg: 0,
            sensors: [],
            gateways: [],
            users: [],
            periods: [
                {
                    value: "hour",
                    title: "ساعتی",
                },
                {
                    value: "day",
                    title: "روزانه",
                },
                {
                    value: "month",
                    title: "ماهانه",
                },
                {
                    value: "year",
                    title: "سالانه",
                },
            ],
            selectedPeriod: "",
            loadingSum: true,
            sumOption: {
                chart: {
                    id: "vuechart-example",
                },
                tooltip: {
                    enabled: false,
                },
                plotOptions: {
                    bar: {
                        borderRadius: 10,
                        dataLabels: {
                            position: 'top', // top, center, bottom
                        },
                    }
                },
                dataLabels: {
                    enabled: true,
                    formatter: function (val) {
                      return val + "W";
                    },
                    offsetY: -20,
                    style: {
                      fontSize: "12px",
                      colors: ['#2082db']
                    },
                  },
                xaxis: {
                    categories: [],
                    labels: {
                        style: {
                            colors: [],
                            fontSize: "12px",
                            fontFamily: "Helvetica, Arial, sans-serif",
                            fontWeight: 400,
                            cssClass: "apexcharts-xaxis-label",
                        },
                    },
                },
                yaxis: {
                    labels: {
                        style: {
                            colors: [],
                            fontSize: "12px",
                            fontFamily: "Helvetica, Arial, sans-serif",
                            fontWeight: 400,
                            cssClass: "apexcharts-xaxis-label",
                        },
                    },
                },
            },
            sumSeries: [
                {
                    name: "series-1",
                    data: [],
                },
            ],
        }
    },

    async mounted() {
        this.loadingAvg = true
        this.sensors = (await sensorServices.getAll()).data
        this.gateways = (await gatewayServices.getAll()).data
        await this.getProfile()
        this.loadingAvg = false
    },
    watch: {
        selectedPeriod() {
            this.getSum()
        }
    },
    methods: {
        async getProfile() {
            this.loading = true;
            try {
                const { data } = await axios({
                    method: "GET",
                    url: "/users",
                    headers: {
                        Authorization: "Bearer " + this.$cookies.get('admin-token'),
                    },
                });
                this.users = data;
            } catch {
                this.$toast.error("خطایی رخ داد");
            } finally {
                this.loading = false;
            }
        },
        async getSum() {
            this.loadingSum = true;
            try {
                const res = await axios({
                    method: "GET",
                    url: `/reports/admin/sum`,
                    params: {
                        period: this.selectedPeriod,
                    },
                    headers: {
                        Authorization: `Bearer ${this.$cookies.get('admin-token')}`,
                    },
                });
                const { data } = res;
                this.sumOption.xaxis.categories = [];
                this.sumSeries[0].data = [];
                data.sort((a, b) => {
                    return new Date(a.time) < new Date(b.time)
                })
                data.forEach((item) => {
                    this.sumOption.xaxis.categories.push(`${this.selectedPeriod !== 'hour' ? '' : new Date(item.time).toLocaleTimeString("fa-IR")} ${new Date(item.time).toLocaleDateString("fa-IR")}`);
                   
                    this.sumSeries[0].data.push(item.total.toFixed(3));
                });
            } catch {
                this.$toast.error("خطایی رخ داد");
            } finally {
                this.loadingSum = false;
            }
        }

    }

}